// Load the user collection data as primary authentication source
let userCollectionData = null;
try {
  // Import the JSON file from src directory (single source of truth)
  userCollectionData = require('../data/adapt-user-collection.json');
  // Silent load - only log errors
} catch (error) {
  console.error(' [AdaptUserService] Failed to load user collection data:', error.message);
  console.log(' [AdaptUserService] User verification will not work without the JSON data file');
}

