class AdaptUserService {
  constructor() {
    this.instances = ADAPT_INSTANCES;
  }

  /**
   * Verify user access from local JSON collection file (primary method)
   * This method uses the pre-collected user data for authentication verification
   * @param {string} email - User's email address
   * @returns {Promise<Array>} Array of accessible instances
   */
  async getUserAccessibleInstances(email) {
    console.log('\n [AdaptUserService] Starting local JSON data verification...');
    console.log(' [AdaptUserService] Email:', email);
    
    if (!userCollectionData || !userCollectionData.instances) {
      console.log(' [AdaptUserService] No local user collection data available');
      return [];
    }

    const normalizedEmail = email.toLowerCase().trim();
    console.log(' [AdaptUserService] Normalized email:', normalizedEmail);
    
    const accessibleInstances = [];
    
    // Check each instance in the local data
    for (const [instanceKey, instanceData] of Object.entries(userCollectionData.instances)) {
      if (instanceData.users && Array.isArray(instanceData.users)) {
        const user = instanceData.users.find(u => 
          u.email && u.email.toLowerCase().trim() === normalizedEmail && u.isActive
        );
        
        if (user) {
          console.log(` [AdaptUserService] User found in local data for ${instanceKey}`);
          accessibleInstances.push({
            id: instanceKey,
            name: instanceData.instanceInfo?.name || this.instances[instanceKey]?.name || instanceKey,
            url: this.instances[instanceKey]?.url || `https://${instanceKey}.laerdal.io`,
            userRole: user.roles ? user.roles.join(', ') : 'User',
            authType: user.authType || 'unknown',
            lastAccess: user.lastAccess,
            userId: user.userId,
            source: 'local-json'
          });
        }
      }
    }
    
    console.log(` [AdaptUserService] Local data verification complete: Found ${accessibleInstances.length} accessible instances`);
    return accessibleInstances;
  }

  /**
   * Extract user role information from roles array
   * @param {Array} roles - Array of role objects or strings
   * @returns {string} User role name
   */
  extractUserRole(roles) {
    if (!roles || roles.length === 0) return "User";
    
    // Handle direct role names
    if (Array.isArray(roles)) {
      // If it's an array of strings, join them
      if (typeof roles[0] === 'string') {
        return roles.join(', ');
      }
      // If it's an array of objects with name property
      if (roles[0] && typeof roles[0] === 'object' && roles[0].name) {
        return roles.map(role => role.name).join(', ');
      }
    }
    
    return "User";
  }
  
  /**
   * Verify user access using local JSON collection data
   * @param {string} email - User's email from SSO or SAP CDC login
   * @param {string} accessToken - Authentication token (for compatibility)
   * @returns {Promise<Object>} Verification result with accessible instances
   */
  async verifyUserAccess(email, accessToken) {
    console.log('\n [AdaptUserService.verifyUserAccess] Starting local JSON-based user verification...');
    console.log(' [AdaptUserService.verifyUserAccess] Email:', email);
    console.log(' [AdaptUserService.verifyUserAccess] Access token length:', accessToken ? accessToken.length : 0);
    console.log(' [AdaptUserService.verifyUserAccess] Verification timestamp:', new Date().toISOString());
    console.log(' [AdaptUserService.verifyUserAccess] Using local JSON collection data');
    
    // Check if JSON data is available
    if (!userCollectionData || !userCollectionData.instances) {
      console.error(' [AdaptUserService.verifyUserAccess] No local user collection data available');
      return {
        isValid: false,
        email: email,
        accessibleInstances: [],
        totalInstances: 0,
        error: 'No user collection data available',
        verifiedAt: new Date().toISOString(),
        queryMethod: 'Local JSON (Data Missing)'
      };
    }

    try {
      // Get instances user has access to from local JSON data
      console.log(' [AdaptUserService.verifyUserAccess] Querying local JSON collection...');
      const accessibleInstances = await this.getUserAccessibleInstances(email);

      const verificationResult = {
        isValid: accessibleInstances.length > 0,
        email: email,
        accessibleInstances: accessibleInstances,
        totalInstances: accessibleInstances.length,
        verifiedAt: new Date().toISOString(),
        queryMethod: 'Local JSON Collection',
        dataSource: {
          collectionVersion: userCollectionData.metadata?.collectionVersion || '1.0.0',
          lastUpdated: userCollectionData.metadata?.lastUpdated,
          totalInstancesInCollection: Object.keys(userCollectionData.instances).length
        }
      };

      console.log(' [AdaptUserService.verifyUserAccess] Final verification result:', {
        isValid: verificationResult.isValid,
        email: verificationResult.email,
        totalInstances: verificationResult.totalInstances,
        queryMethod: verificationResult.queryMethod,
        verifiedAt: verificationResult.verifiedAt
      });

      if (verificationResult.isValid) {
        console.log(' [AdaptUserService.verifyUserAccess] User verification SUCCESSFUL');
        console.log(' [AdaptUserService.verifyUserAccess] User has access to the following instances:');
        verificationResult.accessibleInstances.forEach((instance, index) => {
          console.log(`   ${index + 1}. ${instance.name} (${instance.id}) - Role: ${instance.userRole} - Auth: ${instance.authType}`);
        });
      } else {
        console.log(' [AdaptUserService.verifyUserAccess] User verification FAILED - No access found in collection');
      }

      return verificationResult;
    } catch (error) {
      console.error(' [AdaptUserService.verifyUserAccess] Error during verification:', error);
      
      const errorResult = {
        isValid: false,
        email: email,
        accessibleInstances: [],
        totalInstances: 0,
        error: error.message,
        verifiedAt: new Date().toISOString(),
        queryMethod: 'Local JSON Collection (Failed)'
      };
      
      console.log(' [AdaptUserService.verifyUserAccess] Returning error result:', errorResult);
      return errorResult;
    }
  }
}

const adaptUserService = new AdaptUserService();
export default adaptUserService;
