import React, { useState, useEffect } from 'react';
import { sapCDCService } from '../services/SAPCDCService';
import { Button } from '../../../components/ui/button';

/**
 * SAP CDC Test Component
 * This component provides a simple interface to test SAP CDC functionality
 */
const SAPCDCTest = () => {
  const [status, setStatus] = useState('Not initialized');
  const [user, setUser] = useState(null);
  const [error, setError] = useState(null);
  const [loading, setLoading] = useState(false);

  const updateStatus = (newStatus) => {
    setStatus(newStatus);
    console.log(`[SAP CDC Test] Status: ${newStatus}`);
  };

  const initializeSAPCDC = async () => {
    try {
      setLoading(true);
      setError(null);
      updateStatus('Initializing...');
      
      await sapCDCService.init();
      updateStatus('Initialized successfully');
      
      // Check if already authenticated
      const isAuth = await sapCDCService.isAuthenticated();
      if (isAuth) {
        const userInfo = await sapCDCService.getUserInfo();
        setUser(userInfo);
        updateStatus('Already authenticated');
      }
    } catch (err) {
      setError(err.message);
      updateStatus('Failed to initialize');
    } finally {
      setLoading(false);
    }
  };

  const performLogin = async () => {
    try {
      setLoading(true);
      setError(null);
      updateStatus('Logging in...');
      
      const response = await sapCDCService.login();
      if (response.user) {
        setUser(response.user);
        updateStatus('Login successful');
      }
    } catch (err) {
      setError(err.message);
      updateStatus('Login failed');
    } finally {
      setLoading(false);
    }
  };

  const performLogout = async () => {
    try {
      setLoading(true);
      setError(null);
      updateStatus('Logging out...');
      
      await sapCDCService.logout();
      setUser(null);
      updateStatus('Logged out successfully');
    } catch (err) {
      setError(err.message);
      updateStatus('Logout failed');
    } finally {
      setLoading(false);
    }
  };

  const getToken = async () => {
    try {
      setLoading(true);
      setError(null);
      updateStatus('Getting token...');
      
      const tokenInfo = await sapCDCService.getToken();
      console.log('Token info:', tokenInfo);
      updateStatus('Token retrieved successfully');
    } catch (err) {
      setError(err.message);
      updateStatus('Failed to get token');
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    initializeSAPCDC();
  }, []);

  return (
    <div className="p-6 bg-white rounded-lg shadow-lg max-w-md mx-auto">
      <h2 className="text-xl font-bold mb-4">SAP CDC Test Interface</h2>
      
      <div className="mb-4">
        <div className="text-sm font-medium">Status:</div>
        <div className={`text-sm p-2 rounded ${
          status.includes('success') ? 'bg-green-100 text-green-800' :
          status.includes('fail') || status.includes('error') ? 'bg-red-100 text-red-800' :
          'bg-blue-100 text-blue-800'
        }`}>
          {status}
        </div>
      </div>

      {error && (
        <div className="mb-4">
          <div className="text-sm font-medium">Error:</div>
          <div className="text-sm p-2 rounded bg-red-100 text-red-800">
            {error}
          </div>
        </div>
      )}

      {user && (
        <div className="mb-4">
          <div className="text-sm font-medium">User Info:</div>
          <div className="text-sm p-2 rounded bg-green-100 text-green-800">
            <div>Email: {user.profile?.email}</div>
            <div>Name: {user.profile?.firstName} {user.profile?.lastName}</div>
            <div>Provider: {user.loginProvider}</div>
            <div>UID: {user.UID}</div>
          </div>
        </div>
      )}

      <div className="space-y-2">
        <Button 
          onClick={initializeSAPCDC} 
          disabled={loading}
          className="w-full"
        >
          {loading ? 'Loading...' : 'Initialize SAP CDC'}
        </Button>
        
        <Button 
          onClick={performLogin} 
          disabled={loading || !sapCDCService.isInitialized}
          className="w-full"
        >
          {loading ? 'Loading...' : 'Login with SAP CDC'}
        </Button>
        
        <Button 
          onClick={getToken} 
          disabled={loading || !user}
          className="w-full"
          variant="outline"
        >
          {loading ? 'Loading...' : 'Get Token'}
        </Button>
        
        <Button 
          onClick={performLogout} 
          disabled={loading || !user}
          className="w-full"
          variant="outline"
        >
          {loading ? 'Loading...' : 'Logout'}
        </Button>
      </div>

      <div className="mt-4 text-xs text-gray-500">
        <p>Environment: {process.env.NODE_ENV}</p>
        <p>API Key: {sapCDCService.currentApiKey}</p>
        <p>LocalHost: {sapCDCService.isLocalHost ? 'Yes' : 'No'}</p>
      </div>
    </div>
  );
};

export default SAPCDCTest;
