import React, { useState, useEffect } from "react";
import { useNavigate, useLocation } from "react-router-dom";
import { Card } from "./ui/card";
import { Button } from "./ui/button";
import { Users, House, Rocket, Plus, Search, Clock } from "lucide-react";
import { useAuthService } from "../auth/sso/hooks/useAuthService";
import { useSAPCDC } from "../auth/sapcdc/hooks/useSAPCDC";
import instanceIcon from "../images/Adapt_Logo.jpg";
import cardIcon from "../images/adapt-logo-instance.webp";
import settingIcon from "../images/Settings.svg";
import Header from "./ui/Header";
import Footer from "./ui/Footer";

const InstanceSwitch = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const { logout } = useAuthService();
  const { logout: sapCDCLogout } = useSAPCDC();
  const [instances, setInstances] = useState([]);
  const [searchTerm, setSearchTerm] = useState("");

  // Get user from location state (passed from login page)
  let user = "User";
  let loginType = "Unknown";
  let accessibleInstances = [];
  let verificationResult = null;

  try {
    if (location.state?.user) {
      user = location.state.user;
      loginType = location.state.loginType || "Unknown";
      accessibleInstances = location.state.accessibleInstances || [];
      verificationResult = location.state.verificationResult;
    } else if (window.history.state?.usr) {
      user = window.history.state.usr;
    }
  } catch (e) {
    console.warn("Error parsing user state:", e);
    // fallback to default
  }

  useEffect(() => {
    // If user has verified access from SSO, use those instances
    if (accessibleInstances.length > 0) {
      const mappedInstances = accessibleInstances.map((instance, index) => ({
        id: instance.id,
        name: instance.name,
        role: instance.userRole,
        description: instance.description,
        icon: instanceIcon,
        link: instance.url,
        lastAccess: instance.lastAccess,
        firstAccess: instance.firstAccess,
        isActive: instance.isActive,
      }));
      setInstances(mappedInstances);
    } else {
      // Fallback to default instances for non-SSO users
      setInstances([
        {
          id: "instance-a",
          name: "Dev Next Instance",
          role: "Admin",
          description: "Production course builder",
          icon: instanceIcon,
          link: "https://adapt-dev-next.laerdal.io/#dashboard",
        },
        {
          id: "instance-b",
          name: "Tryout Instance",
          role: "Reviewer",
          description: "Testing and review only",
          icon: instanceIcon,
          link: "https://adapt-tryout.laerdal.io/#dashboard",
        },
      ]);
    }
  }, [accessibleInstances]);

  const formatDate = (dateString) => {
    if (!dateString) return "Never";
    return new Date(dateString).toLocaleDateString("en-US", {
      year: "numeric",
      month: "short",
      day: "numeric",
    });
  };

  // Enhanced instance navigation with SSO token support
  const handleInstanceAccess = async (instance) => {
    try {
      console.log(`\n [Instance Access] Accessing ${instance.name}...`);
      console.log(" [Instance Access] Instance details:", {
        id: instance.id,
        name: instance.name,
        url: instance.link,
        role: instance.role,
      });

      // For SSO users, attempt to pass authentication context
      if (loginType === "Azure AD SSO" && verificationResult) {
        console.log(
          " [Instance Access] SSO user detected, preparing authentication context..."
        );

        // Option 1: Direct dashboard access with user context
        const dashboardUrl = `${
          instance.link
        }?sso=true&user=${encodeURIComponent(
          location.state?.email || user
        )}&token=${Date.now()}`;

        console.log(" [Instance Access] Opening dashboard URL:", {
          baseUrl: instance.link,
          withSSO: dashboardUrl,
          userEmail: location.state?.email || user,
        });

        // Open the dashboard directly
        window.open(dashboardUrl, "_blank");

        // Log successful access
        console.log(
          " [Instance Access] Successfully opened dashboard for SSO user"
        );
      } else {
        // Fallback for non-SSO users
        console.log(
          " [Instance Access] Non-SSO user, using standard navigation..."
        );
        window.open(instance.link, "_blank");
      }
    } catch (error) {
      console.error(" [Instance Access] Failed to access instance:", error);
      // Fallback to standard navigation
      window.open(instance.link, "_blank");
    }
  };

  // Filter instances based on search term
  const filteredInstances = instances.filter(instance => {
    const searchLower = searchTerm.toLowerCase();
    const instanceName = (instance?.name || '').toLowerCase();
    const instanceDescription = (instance?.description || '').toLowerCase();
    const instanceRole = (instance?.role || 'Course Creator').toLowerCase();
    
    return instanceName.includes(searchLower) ||
           instanceDescription.includes(searchLower) ||
           instanceRole.includes(searchLower);
  });

  const handleLogout = async () => {
    try {
      console.log("\n [Instance Switch] Starting logout process...");
      console.log(" [Instance Switch] Login type:", loginType);

      if (loginType.includes("Azure AD") || loginType.includes("SSO")) {
        console.log(" [Instance Switch] Performing Azure AD SSO logout...");
        // Use Azure AD logout for SSO users
        await logout();
      } else if (loginType === "SAP CDC") {
        console.log(" [Instance Switch] Performing SAP CDC logout...");
        // Use SAP CDC logout for SAP CDC users
        await sapCDCLogout();
        console.log(" [Instance Switch] SAP CDC logout completed");
        alert("SAP CDC logout successful!");
        navigate("/");
      } else {
        console.log(" [Instance Switch] Performing standard logout...");
        // Regular logout for form-based users
        alert("Logging out...");
        navigate("/");
      }
    } catch (error) {
      console.error(" [Instance Switch] Logout error:", error);
      // Fallback logout
      alert("Logout completed with warnings. Redirecting to login page...");
      navigate("/");
    }
  };

  return (
    <section className="min-h-screen bg-white flex flex-col justify-between">
      <Header user={user} onLogout={handleLogout} showUserDropdown={true} />
      <div className="flex flex-1 flex-col items-center bg-white px-4 py-8">
        <div className="w-full max-w-5xl">
          <div className="flex flex-col md:flex-row md:items-center md:justify-between mb-12 border-b-2 border-brand-blue pb-6">
            <div>
              <h1 className="text-3xl font-bold mb-2 flex items-center gap-2">
                <span className="flex items-center justify-center h-10 w-10 bg-primary-100 text-primary-600 rounded-full p-1 mr-2">
                  <House />
                </span>{" "}
                Welcome to Adapt Authoring Tool
              </h1>
              <p className="text-gray-500 mb-2 text-left font-bold">
                Your dashboard shows your current instance access. Request
                access for instance(s) and start creating amazing learning
                experiences.
              </p>
            </div>
          </div>

          {/* Search and Instance Count Section */}
          <div className="flex flex-col gap-4 mb-8">
            <h2 className="text-2xl font-bold text-left text-gray-600 tracking-tight">
              Your Instances ({instances.length})
            </h2>
            
            {instances.length > 0 && (
              <div className="relative w-full">
                <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                  <Search className="h-5 w-5 text-gray-400" />
                </div>
                <input
                  type="text"
                  placeholder="Search instances by name or type..."
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                  className="block w-full pl-10 pr-3 py-3 border border-gray-300 rounded-lg leading-5 bg-white placeholder-gray-500 focus:outline-none focus:placeholder-gray-400 focus:ring-2 focus:ring-primary-400 focus:border-primary-400 sm:text-sm transition-colors"
                />
              </div>
            )}
          </div>

          {instances.length === 0 ? (
            <div className="flex flex-col items-center justify-center py-16">
              <div className="text-center">
                <Users className="w-16 h-16 text-gray-400 mx-auto mb-4" />
                <h3 className="text-lg font-semibold text-gray-600 mb-2">
                  No Instances Available
                </h3>
                <p className="text-gray-500">
                  You don't have access to any Adapt Learning instances.
                </p>
                <p className="text-gray-500 mt-2">
                  Please contact your administrator to request access.
                </p>
              </div>
            </div>
          ) : (
            <>
              {filteredInstances.length === 0 && searchTerm ? (
                <div className="flex flex-col items-center justify-center py-16">
                  <div className="text-center">
                    <Search className="w-16 h-16 text-gray-400 mx-auto mb-4" />
                    <h3 className="text-lg font-semibold text-gray-600 mb-2">
                      No instances found
                    </h3>
                    <p className="text-gray-500">
                      No instances match your search "{searchTerm}".
                    </p>
                    <p className="text-gray-500 mt-2">
                      Try searching with different keywords.
                    </p>
                  </div>
                </div>
              ) : (
                <div className="flex flex-col gap-8">
                  <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-8">
                    {filteredInstances.map((instance, index) => (
                  <article
                    key={instance.id}
                    className="card shadow-xl border border-gray-100 w-full min-h-[340px] rounded-2xl overflow-hidden transition-all duration-300 hover:shadow-2xl hover:scale-[1.02] focus-within:ring-2 focus-within:ring-blue-500 focus-within:ring-offset-2 group"
                    aria-labelledby={`instance-title-${instance.id}`}
                    aria-describedby={`instance-desc-${instance.id}`}
                  >
                    {/* Top: Role badge and logo, beige background */}
                    <div
                      className="bg-[#FFF4E8] flex flex-col items-center relative overflow-hidden"
                      style={{ minHeight: "120px" }}
                    >
                      <img
                        src={cardIcon}
                        alt={`${instance.name} logo`}
                        className="object-contain transition-transform duration-300 group-hover:scale-110"
                      />
                      <span
                        className="absolute left-4 top-4 flex items-center gap-1 text-xs font-semibold bg-[#F8E2BF] text-[#806128] px-2 py-1 rounded-md shadow border border-[#F8E2BF]"
                        aria-label={`User role: ${
                          instance.role || "Course Creator"
                        }`}
                      >
                        {instance.role === "Super Admin" && (
                          <svg
                            xmlns="http://www.w3.org/2000/svg"
                            className="h-4 w-4 mr-1"
                            fill="currentColor"
                            viewBox="0 0 20 20"
                            aria-hidden="true"
                          >
                            <path d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.286 3.966a1 1 0 00.95.69h4.175c.969 0 1.371 1.24.588 1.81l-3.38 2.455a1 1 0 00-.364 1.118l1.287 3.966c.3.921-.755 1.688-1.54 1.118l-3.38-2.455a1 1 0 00-1.175 0l-3.38 2.455c-.784.57-1.838-.197-1.539-1.118l1.287-3.966a1 1 0 00-.364-1.118L2.049 9.393c-.783-.57-.38-1.81.588-1.81h4.175a1 1 0 00.95-.69l1.286-3.966z" />
                          </svg>
                        )}
                        {instance.role || "Course Creator"}
                      </span>
                    </div>
                    {/* Bottom: Info section, white background */}
                    <div className="flex flex-col justify-between px-6 py-5 bg-white text-left rounded-b-2xl" style={{ minHeight: "180px" }}>
                      <div className="flex-1">
                        <h3
                          id={`instance-title-${instance.id}`}
                          className="text-xl font-bold mb-1 text-gray-900"
                        >
                          {instance.name}
                        </h3>
                        <p
                          id={`instance-desc-${instance.id}`}
                          className="text-sm text-gray-600 mb-2"
                        >
                          {instance.description}
                        </p>
                        <hr className="my-3 border-gray-200" />
                        <div className="mb-2">
                          <div className="text-sm font-semibold text-gray-700">
                            Last Access
                          </div>
                          <div className="text-xs text-gray-500">
                            <Clock className="inline-block mr-1 w-3 h-3" />
                            {instance.lastAccess
                              ? formatDate(instance.lastAccess)
                              : "Just Now"}
                          </div>
                        </div>
                      </div>
                      <Button
                        className="mt-4 w-full bg-brand-blue hover:bg-brand-darkBlue text-white font-semibold rounded-lg flex items-center justify-center gap-2 text-base py-2"
                        onClick={() => handleInstanceAccess(instance)}
                        disabled={!instance.isActive}
                        aria-label={`Launch ${instance.name} instance${
                          !instance.isActive ? " (currently disabled)" : ""
                        }`}
                      >
                       <Rocket className="w-5 h-5 mr-1" />
                        Launch
                      </Button>
                    </div>
                  </article>
                ))}
              </div>
            </div>
              )}
            </>
          )}

          {/* Request More Access Section */}
          <div className="mt-10 mb-8 border-2 border-dashed border-brand-blue rounded-xl p-6 bg-white flex flex-col items-center">
            <div className="flex items-center gap-2 mb-2 text-brand-blue font-medium">
              <span className="flex items-center justify-center h-10 w-10 bg-primary-100 text-primary-600 rounded-full p-1 mr-2">
              <Plus className="w-6 h-6" />
              </span>
            </div>
            <p className="text-gray-600 mb-3 text-center">
              Need access to additional instance for your projects?
            </p>
            <Button
              className="bg-brand-blue hover:bg-brand-darkBlue focus:bg-brand-darkBlue focus:ring-2 focus:ring-primary-400 focus:ring-offset-2 text-white px-6 py-2 rounded font-semibold border-none shadow-none transition-colors"
              onClick={() => alert("Request More Access feature coming soon!")}
              onKeyDown={(e) => {
                if (e.key === "Enter" || e.key === " ") {
                  e.preventDefault();
                  alert("Request More Access feature coming soon!");
                }
              }}
              aria-label="Request access to additional instances"
              tabIndex={0}
            >
              Request Instance
            </Button>
          </div>

          {/* Quick Start Guide Section */}
          {/* <div className="border-2 border-gray-200 rounded-xl p-6 bg-white">
            <div className="flex items-center gap-2 mb-3 text-[#2E7FA1] font-medium">
              <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" fill="none" viewBox="0 0 24 24" stroke="currentColor"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 12h6m-3-3v6m9-9a9 9 0 11-18 0 9 9 0 0118 0z" /></svg>
              Quick Start Guide
            </div>
            <p className="text-gray-600 mb-4">Get started with your instance access</p>
            <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
              <div className="flex flex-col items-center">
                <div className="bg-[#2E7FA1] text-white rounded-full w-8 h-8 flex items-center justify-center font-bold mb-2">1</div>
                <div className="font-semibold mb-1 text-gray-800">Access Your Instance</div>
                <div className="text-xs text-gray-500 text-center">Click "Go to Dev Instance" to open your development environment.</div>
              </div>
              <div className="flex flex-col items-center">
                <div className="bg-[#2E7FA1] text-white rounded-full w-8 h-8 flex items-center justify-center font-bold mb-2">2</div>
                <div className="font-semibold mb-1 text-gray-800">Create Content</div>
                <div className="text-xs text-gray-500 text-center">Start building interactive learning modules and courses.</div>
              </div>
              <div className="flex flex-col items-center">
                <div className="bg-[#2E7FA1] text-white rounded-full w-8 h-8 flex items-center justify-center font-bold mb-2">3</div>
                <div className="font-semibold mb-1 text-gray-800">Collaborate</div>
                <div className="text-xs text-gray-500 text-center">Invite team members and work together on projects.</div>
              </div>
            </div>
          </div> */}
        </div>
      </div>
      <Footer />
    </section>
  );
};

export default InstanceSwitch;
