/**
 * MongoDB Query Test and Documentation
 * 
 * This file demonstrates the exact MongoDB queries that are executed
 * during the SSO authentication and user verification process.
 * 
 * Run this test to see detailed logs of all database operations.
 */

import AdaptUserService from '../users/services/AdaptUserService';
import logger from '../utils/logger';

/**
 * Test the complete SSO flow with detailed logging
 */
export const testSSOFlow = async (testEmail = 'vadiraj.joshi@laerdal.com') => {
  console.clear();
  console.log(' SSO Authentication Flow Test');
  console.log('================================');
  console.log(` Testing with email: ${testEmail}`);
  console.log(` Test started at: ${new Date().toISOString()}`);
  
  // Log the MongoDB query templates
  logger.logMongoQueryTemplates();
  
  console.log('\n Starting Live Test with Simulated MongoDB Queries...\n');
  
  try {
    // Step 1: Simulate SSO Login Success
    console.log(' Step 1: Azure AD SSO Authentication');
    console.log(' Azure AD authentication successful');
    console.log(' User details extracted from token');
    
    // Step 2: Start User Verification
    logger.verificationStart(testEmail);
    
    // Step 3: Query each MongoDB instance
    const mockAccessToken = 'mock_token_from_azure_ad';
    const verificationResult = await AdaptUserService.verifyUserAccess(testEmail, mockAccessToken);
    
    // Step 4: Display final results
    console.log('\n FINAL VERIFICATION RESULTS');
    console.log('=============================');
    console.log(' User Access Status:', verificationResult.isValid ? 'GRANTED' : 'DENIED');
    console.log(' Email:', verificationResult.email);
    console.log(' Total Accessible Instances:', verificationResult.totalInstances);
    console.log(' Verified At:', verificationResult.verifiedAt);
    
    if (verificationResult.accessibleInstances.length > 0) {
      console.log('\n ACCESSIBLE INSTANCES:');
      verificationResult.accessibleInstances.forEach((instance, index) => {
        console.log(`\n${index + 1}. ${instance.name}`);
        console.log(`   - Instance ID: ${instance.id}`);
        console.log(`   - URL: ${instance.url}`);
        console.log(`   - User Role: ${instance.userRole}`);
        console.log(`   - Last Access: ${instance.lastAccess}`);
        console.log(`   - First Access: ${instance.firstAccess}`);
        console.log(`   - Status: ${instance.isActive ? 'Active' : 'Inactive'}`);
      });
    }
    
    logger.verificationEnd();
    
    return verificationResult;
    
  } catch (error) {
    console.error(' Test failed:', error);
    return null;
  }
};

/**
 * Test individual MongoDB instance queries
 */
export const testIndividualInstances = async (testEmail = 'vadiraj.joshi@laerdal.com') => {
  console.log('\n INDIVIDUAL INSTANCE TESTING');
  console.log('==============================');
  
  const instances = ['adapt-dev', 'adapt-tryout'];
  
  for (const instanceKey of instances) {
    console.log(`\n Testing Instance: ${instanceKey}`);
    console.log(''.repeat(40));
    
    try {
      const result = await AdaptUserService.queryUserInInstance(testEmail, instanceKey);
      
      if (result) {
        console.log(' USER FOUND:');
        console.log(`   - Email: ${result.email}`);
        console.log(`   - ID: ${result._id}`);
        console.log(`   - Auth Type: ${result.auth}`);
        console.log(`   - ID Number: ${result.idNumber}`);
        console.log(`   - Active: ${!result._isDeleted}`);
        console.log(`   - Roles: ${result.roles?.length || 0}`);
        console.log(`   - First Access: ${result.firstAccess}`);
        console.log(`   - Last Access: ${result.lastAccess}`);
        console.log(`   - Session Count: ${result.sessions?.length || 0}`);
      } else {
        console.log(' USER NOT FOUND');
      }
    } catch (error) {
      console.error(` Error querying ${instanceKey}:`, error.message);
    }
  }
};

/**
 * Production MongoDB Query Examples
 * These show what the actual queries would look like in a real backend
 */
export const showProductionQueries = () => {
  console.log('\n PRODUCTION MONGODB QUERIES');
  console.log('=============================');
  
  const exampleEmail = 'vadiraj.joshi@laerdal.com';
  
  console.log('\n1.  USER LOOKUP QUERY:');
  console.log('Collection: users');
  console.log('Query:');
  const userQuery = {
    email: { $regex: new RegExp(`^${exampleEmail}$`, 'i') },
    _isDeleted: false
  };
  console.log(JSON.stringify(userQuery, null, 2));
  
  console.log('\n2.  AGGREGATION WITH ROLES:');
  console.log('Pipeline:');
  const aggregationPipeline = [
    {
      $match: {
        email: { $regex: new RegExp(`^${exampleEmail}$`, 'i') },
        _isDeleted: false
      }
    },
    {
      $lookup: {
        from: 'roles',
        localField: 'roles',
        foreignField: '_id',
        as: 'roleDetails'
      }
    },
    {
      $project: {
        _id: 1,
        email: 1,
        auth: 1,
        firstAccess: 1,
        lastAccess: 1,
        idNumber: 1,
        _isDeleted: 1,
        'roleDetails.name': 1,
        sessionCount: { $size: '$sessions' }
      }
    }
  ];
  console.log(JSON.stringify(aggregationPipeline, null, 2));
  
  console.log('\n3.  UPDATE LAST ACCESS:');
  const updateQuery = {
    $set: {
      lastAccess: new Date(),
      $inc: { loginCount: 1 }
    }
  };
  console.log(JSON.stringify(updateQuery, null, 2));
  
  console.log('\n4.  CONNECTION DETAILS:');
  console.log('Adapt Dev Instance:');
  console.log('  - Host: 52.50.122.136');
  console.log('  - Port: 27017');
  console.log('  - Database: adapt');
  console.log('  - Collection: users');
  console.log('  - Full URI: mongodb://52.50.122.136:27017/adapt');
  
  console.log('\nAdapt Tryout Instance:');
  console.log('  - Host: 52.50.122.136');
  console.log('  - Port: 27017');
  console.log('  - Database: adapt_tryout');
  console.log('  - Collection: users');
  console.log('  - Full URI: mongodb://52.50.122.136:27017/adapt_tryout');
};

/**
 * Run complete test suite
 */
export const runCompleteTest = async (testEmail = 'vadiraj.joshi@laerdal.com') => {
  console.clear();
  console.log(' COMPLETE SSO + MONGODB TEST SUITE');
  console.log('====================================');
  console.log(` ${new Date().toLocaleString()}`);
  console.log(` Test Email: ${testEmail}\n`);
  
  // Show production queries
  showProductionQueries();
  
  // Test individual instances
  await testIndividualInstances(testEmail);
  
  // Test complete flow
  await testSSOFlow(testEmail);
  
  console.log('\n Test suite completed!');
  console.log('Check the console logs above for detailed MongoDB query information.');
};

// Export for use in browser console
window.testSSO = {
  runCompleteTest,
  testSSOFlow,
  testIndividualInstances,
  showProductionQueries
};

export default {
  runCompleteTest,
  testSSOFlow,
  testIndividualInstances,
  showProductionQueries
};
