/**
 * Teams Notification Service
 * Handles sending notifications to Microsoft Teams via local server proxy
 * This service calls the local Teams server to bypass CORS restrictions
 */

class TeamsNotificationService {
  constructor() {
    this.localServerUrl = 'http://localhost:3001';
  }

  /**
   * Send account creation notification to Teams
   * @param {Object} userDetails - User account creation details
   * @returns {Promise<Object>} Notification result
   */
  async sendAccountCreationNotification(userDetails) {
    try {
      console.log(' [TeamsNotificationService] Sending account creation notification...');
      console.log(' [TeamsNotificationService] User details:', userDetails);

      // Call local Teams server
      const response = await fetch(`${this.localServerUrl}/api/teams/notify`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          firstName: userDetails.firstName,
          lastName: userDetails.lastName,
          email: userDetails.email,
          companyName: userDetails.companyName,
          reasonForUse: userDetails.reasonForUse,
          submittedAt: userDetails.submittedAt || new Date().toLocaleString()
        })
      });

      const result = await response.json();

      if (response.ok && result.success) {
        console.log(' [TeamsNotificationService] Teams notification sent successfully');
        return {
          success: true,
          message: result.message || 'Teams notification sent successfully',
          data: result
        };
      } else {
        console.warn(' [TeamsNotificationService] Teams notification failed:', result.error);
        return {
          success: false,
          error: result.error || 'Failed to send Teams notification',
          suggestion: 'Please check the Teams server status and try again.'
        };
      }
    } catch (error) {
      console.error(' [TeamsNotificationService] Network or service error:', error);
      return {
        success: false,
        error: `Service error: ${error.message}`,
        suggestion: 'Please check if the Teams notification server is running and try again.'
      };
    }
  }

  /**
   * Test the Teams notification service
   * @returns {Promise<Object>} Test result
   */
  async testNotification() {
    const testUserDetails = {
      firstName: 'Test',
      lastName: 'User',
      email: 'test.user@laerdal.com',
      companyName: 'Laerdal Medical',
      reasonForUse: 'Testing Teams notification service',
      submittedAt: new Date().toLocaleString()
    };

    return await this.sendAccountCreationNotification(testUserDetails);
  }

  /**
   * Get service health status
   * @returns {Promise<Object>} Health status
   */
  async getHealthStatus() {
    try {
      const response = await fetch(`${this.localServerUrl}/api/health`);
      const result = await response.json();
      return {
        success: response.ok,
        status: result,
        serverUrl: this.localServerUrl
      };
    } catch (error) {
      return {
        success: false,
        error: `Health check failed: ${error.message}`,
        serverUrl: this.localServerUrl
      };
    }
  }
}

// Export singleton instance
const teamsNotificationService = new TeamsNotificationService();
export default teamsNotificationService;