import React, { useEffect } from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import { PublicClientApplication } from '@azure/msal-browser';
import { MsalProvider } from '@azure/msal-react';
import { msalConfig } from './auth/sso/providers/authProvider';
import './App.css';
import LandingPage from './components/LandingPage';
import SSOLogin from './auth/sso/components/SSOLogin';
import SAPCDCLogin from './auth/sapcdc/components/SAPCDCLogin';
import SAPCDCTest from './auth/sapcdc/components/SAPCDCTest';
import SignUp from './components/SignUp';
import InstanceSwitch from './components/Adapt-Instances';
import MsalTokenProvider from './auth/sso/contexts/MsalTokenProvider';
import TokenHelper from './auth/sso/components/TokenHelper';

// Development imports
import mongoTestSuite from './dev/mongoTestSuite';
import logger from './utils/logger';

// Create MSAL instance
const msalInstance = new PublicClientApplication(msalConfig);

function App() {
  useEffect(() => {
    // Development mode setup - Reduced logging for performance
    if (process.env.NODE_ENV === 'development') {
      console.log(' Adapt Authentication App - Development Mode');
      
      // Make test suite available globally (silent)
      window.testSSO = mongoTestSuite;
      window.logger = logger;
      
      // Log essential info only
      console.log(' Test commands: window.testSSO.runCompleteTest()');
    }
  }, []);

  return (
    <MsalProvider instance={msalInstance}>
      <MsalTokenProvider>
        <TokenHelper>
          <Router>
            <div className="App">
              <Routes>
                <Route path="/" element={<LandingPage />} />
                <Route path="/sso-login" element={<SSOLogin />} />
                <Route path="/sapcdc-login" element={<SAPCDCLogin />} />
                <Route path="/sapcdc-test" element={<SAPCDCTest />} />
                <Route path="/signup" element={<SignUp />} />
                <Route path="/instances" element={<InstanceSwitch />} />
              </Routes>
            </div>
          </Router>
        </TokenHelper>
      </MsalTokenProvider>
    </MsalProvider>
  );
}

export default App;
